import React from 'react';

function Grid({ numRows, numColumns, grid }) {
    // Create an array of rows, each containing cells
    const rows = [];
    
    for (let row = 0; row < numRows; row++) {
        const cells = [];
        for (let col = 0; col < numColumns; col++) {
            const key = `${row},${col}`;
          const square = grid[key]; // Get square data from the grid
          if (square) {
            cells.push(
              <div key={key} className="cell" style={{ backgroundColor: `#${square.color.toString(16)}` }}>
                {square.occupied ? 'X' : ''}
              </div>
            );
          }
        }

      
      rows.push(
            <div key={row} className="row">
                {cells}
            </div>
        );
    }
        console.log(
          "rows",rows)

    return (
        <div className="grid">
          
            {rows}
        </div>
    );
}

export default Grid;
