import React, { useState, useEffect } from 'react';
import * as Colyseus from "colyseus.js";
import Grid from './Grid';
import '../style/Grid.css';

function Multiplayer() {
    // Initialize state to hold room state
    const [roomState, setRoomState] = useState(null);

    useEffect(() => {
        // Define an async function to handle asynchronous logic
        const connectToRoom = async () => {
            try {
                // Connect to the Colyseus server
                // const client = new Colyseus.Client('ws://10.0.1.31:2567');
                const client = new Colyseus.Client('ws://localhost:2567');

                // Join or create a room
                const room = await client.joinOrCreate("my_room");
                console.log(room.sessionId, "joined", room.name);

                // Listen for state changes in the room
                room.onStateChange.once((state) => {
                    setRoomState(state)
                    }
                );
                room.onStateChange((state) => {
                    console.log(room.name, "has new state:", state);
                    // Update React state with new room state
                    setRoomState(prevState => ({ ...prevState, ...state }));;
                });
                // Listen for errors in the room
                room.onError((code, message) => {
                    console.log(client.id, "couldn't join", room.name);
                });

                // Listen for leaving events
                room.onLeave((code) => {
                    console.log(client.id, "left", room.name);
                });
            } catch (e) {
                console.log("JOIN ERROR", e);
            }
        };

        // Call the async function
        connectToRoom();

        // Clean up on component unmount
        return () => {
            // Perform cleanup actions if necessary
        };
    }, []); // Run effect only once on component mount

    return (
        <div>
            {/* Render UI based on roomState */}
            {roomState && (
                <div>
                    {/* Render UI elements using roomState */}
                    <h1>{roomState.numRows}</h1>
                    <p>{roomState.numColumns}</p>
                    <p>{JSON.stringify(roomState)}</p>
                    <Grid numRows={roomState.numRows} numColumns={roomState.numColumns} grid={roomState.grid} />
                </div>
            )}
        </div>
    );
}

export default Multiplayer;
